.App {
  margin: 0;
}

.AppHeader {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 42px;
  background-color: #282c34;
  color: white;
  display: flex;
  justify-content: center;
}

.AppHeader-title {
  margin: 0 10px;
  display: flex;
  align-items: center;
  height: 42px;
  font-size: 32px;
}

.AppHeader-version {
  display: flex;
  align-items: flex-end;
  height: 42px;
  font-size: 16px;
}


.Game {
  position: relative;
  margin: 32px auto;
  width: 960px;
  height: 540px;
  background-color: #efefef;
}

.Game-field {
  position: absolute;
  width: 192px;
  height: 416px;
  background-image: url(data:image/png;base64,);
  border: 4px solid white;
}

.Game-field-active {
  border: 4px solid blue;
}

.Game-pos-field1 {
  left: 132px;
  top: 64px;
}

.Game-pos-field2 {
  right: 132px;
  top: 64px;
}

.Game-history {
  position: absolute;
}

.Game-history-tile {
  position: absolute;
  height: 30px;
  width: 120px;
  background-color: white;
  border: 1px solid black;
}

.Game-pos-history1 {
  position: absolute;
  left: 12px;
  top: 64px;
}

.Game-pos-history2 {
  position: absolute;
  right: 132px;
  top: 64px;
}

